body {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
}

.App {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
}

button {
    padding: 16px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    background-color: black;
    color: white;
    border-radius: 8px;
    cursor: pointer;
}

h3,
h4 {
    margin-bottom: 0;
}

nav {
    border-bottom: 4px solid black;
    text-align: center;
    width: 100%;
    background: white;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

nav li {
    display: inline-block;
    padding-top: 24px;
    padding-bottom: 24px;
}

nav a {
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding: 24px 32px;
}

nav a:hover {
    background: black;
    color: white;
}

#page-body {
    max-width: 700px;
    margin: auto;
    padding-top: 72px;
}

.article-list-item {
    text-decoration: none;
    color: black;
    padding: 16px 0 0 0;
}

.article-list-item p {
    padding-bottom: 32px;
    border-bottom: 2px solid black;
}

#add-comment-form {
    border-bottom: 2px solid black;
    padding: 16px;
}

#add-comment-form h3 {
    margin-bottom: 16px;
}

input,
textarea {
    display: block;
    width: 300px;
    padding: 8px;
    border-radius: 8px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
    border: 2px solid black;
    margin-bottom: 16px;
}

.error {
    padding: 8px;
    border-radius: 4px;
    background-color: pink;
}

#add-comment-form button {
    width: 320px;
}

.upvotes-section {
    display: flex;
    flex-direction: row;
}

.upvotes-section button,
p {
    margin-right: 16px;
}

.comment {
    border-bottom: 2px solid black;
    padding: 16px;
}

.nav-right {
    position: absolute;
    top: 8px;
    right: 8px;
}
